import Vue from "vue";
import Vuetify from "vuetify";
import 'vuetify/dist/vuetify.min.css';
import App from "./App.vue";
import router from "./router";
import store from "./store/store.js";
import Toasted from "vue-toasted";
import Loading from 'vue-loading-overlay'; 
import 'vue-loading-overlay/dist/vue-loading.css';
import 'vuetify/dist/vuetify.min.css';
Vue.config.productionTip=false;
Vue.use(require('vue-moment'));
Vue.use(Toasted, {
    duration: 2500
});

import Echo from 'laravel-echo';
window.Pusher=require("pusher-js");
window.Echo = new Echo({
    broadcaster: 'pusher',
    key: 'fcafd01d6d4172b5c5bb',
    cluster: 'ap2',
    forceTLS: true,
    // wsHost: 'https://staging.mistrimama.com/backend/',
    // wsPort: 6001,
    // disableStats: true,
    auth: {
        headers: {
            Authorization: "Bearer " + localStorage.d_token
        }
    }
});

import VueSocialSharing from 'vue-social-sharing';
Vue.use(VueSocialSharing);

// import VueAnalytics from 'vue-analytics'

// Vue.use(VueAnalytics, {
//   id: 'UA-152891154-1'
// })

Vue.config.productionTip=false;
Vue.use(Loading, {
    color: '#ec6523 ',
    loader: 'spinner',
    width: 64,
    height: 64,
    backgroundColor: '#ffffff',
    opacity: 0.5,
    zIndex: 999,
}, { 
})

Vue.use(Vuetify, {
    theme: {
        options: {
            customProperties: true
        },
        primary: "#febe00",
        secondary: "#6a6c6d",
        accent: "#03a9f4",
        error: "#f44336",
        warning: "#ffc107",
        info: "#ffeb3b",
        success: "#4caf50"
    }
});


Vue.mixin( {
    methods: {
        localSetItem: function(key, data) {
            localStorage.setItem(key, JSON.stringify(data));
        },
        localGetItem: function (key) {
            let data=localStorage.getItem(key) || null;
            return JSON.parse(data);
        },
        baseUrl: function () {
            return "https://staging.mistrimama.com/backend/api/";
        },
        errorAlerts(error) {
            if (error.response) {
                return error.response.data.message;
            }
            else {
                return error.message;
            }
        },
        goto(route) {
            this.$router.replace(this.$route.query.redirect || route);
        }
    }
});

new Vue( {
    router, store, render: h=> h(App)
}

).$mount("#app");